%button {
  border-radius: 40px;
  font-size: 20px;
  background: none;
  border: 2px solid #fff;
  font-family: $fontMontserrat;
  display: inline-block;
  transition: $transition;
  max-width: 100%;
  text-align: center;
}

button {
  line-height: 1 !important;
}

.button-green {
  @extend %button;
  color: #fff;
  height: 80px;
  border-color: rgba($colorGreen, .4);
  line-height: 78px;
  font-weight: 300;
  .icon {
    color: $colorGreen;
    margin-right: 15px;
    line-height: 1;
    position: relative;
  }
  &:hover {
    border-color: $colorGreen;
  }
  @include break(tablet) {
    width: 100% !important;
    margin-left: 0 !important;
    margin-right: 0 !important;
  }
}

.button-blue {
  @extend %button;
  height: 46px;
  border-color: rgba(8,98,117,.4);
  line-height: 43px;
  padding: 0 24px;
  color: #00a6c4;
  font-weight: 400;
  &:hover {
    border-color: #00a6c4;
  }
  @include break(mobile){
    width: 100% !important;
    margin-left: 0 !important;
    margin-right: 0 !important;
  }
}