.popup {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999;
  width: 100vw;
  background: #ededed;
  min-height: 100vh;
}

.popup .content,.popup.mobile,.popup.mobile .content {
  position: absolute;
  width: 100%
}

.popup .cover {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 10
}

.popup .content {
  top: 50%;
  left: 50%;
  z-index: 20;
  max-width: 700px;
  padding: 70px 20px 60px;
  -webkit-transform: translate(-50%,-50%);
  -ms-transform: translate(-50%,-50%);
  -o-transform: translate(-50%,-50%);
  transform: translate(-50%,-50%);
  font-size: 16px;
  line-height: 1.44;
  color: #182a36;
  text-align: center;
  display: block;
}

.popup .content input {
  border-color: rgba(8,98,117,.4)
}

.popup .content .close {
  opacity: 1;
  position: absolute;
  top: 0;
  right: 0;
  cursor: pointer;
  padding: 10px;
  color: #CC3232;
  font-size: 23px;
  span{
    display: block;
  }
}

.popup .content .title {
  font-size: 20px;
  font-weight: 700;
  margin-bottom: 1em
}

.popup .content.video {
  max-width: 840px
}


.popup.mobile {
  height: 100%;
  bottom: 0
}

.popup.mobile .content {
  top: 50%;
  max-width: 700px;
  padding: 60px 20px 40px;
  -webkit-transform: translate(-50%,0);
  -ms-transform: translate(-50%,0);
  -o-transform: translate(-50%,0);
  transform: translate(-50%,0)
}
