.header {
  padding: 19px 0;
  border-bottom: 2px solid rgba(4, 144, 171, .2);
  .logo {
    float: left;
    height: 24px;
    @include break(mobile-sm) {
      text-align: center;
      width: 100%;
      margin-bottom: 20px;
    }
    img {
      width: 140px;
    }
  }

  .socials {
    float: right;
    height: 24px;
    @include break(mobile-sm) {
      width: 100%;
      text-align: center;
    }

    a {
      color: rgba($colorCyan, .4);
      font-size: 24px;
      display: inline-block;
      margin: 0 8.5px;
      transition: $transition;
      &:hover {
        color: $colorCyan;
      }

      &:last-of-type {
        margin-right: 0;
      }
      @include break(mobile) {
        margin: 0 3px;
      }
    }
  }
}

