@charset "UTF-8";

@font-face {
  font-family: "gnosis-icons";
  src:url("../fonts/gnosis-icons.eot");
  src:url("../fonts/gnosis-icons.eot?#iefix") format("embedded-opentype"),
  url("../fonts/gnosis-icons.woff") format("woff"),
  url("../fonts/gnosis-icons.ttf") format("truetype"),
  url("../fonts/gnosis-icons.svg#gnosis-icons") format("svg");
  font-weight: normal;
  font-style: normal;

}

[data-icon]:before {
  font-family: "gnosis-icons" !important;
  content: attr(data-icon);
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

[class^="icon-"]:before,
[class*=" icon-"]:before {
  font-family: "gnosis-icons" !important;
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

