html {
  font-size: 62.5%;
}

body {
  font-family: $fontMontserrat;
  font-weight: 300;
  @include rem(font-size, 1.6rem);
  color: #87949c;
  -webkit-font-smoothing: antialiased;
  position: relative;
}

a {
  text-decoration: none;
}

input {
  font-family: $fontMontserrat !important;

  &::-webkit-input-placeholder {
    color: rgba($colorCyan, .2);
  }
  &::-moz-placeholder {
    color: rgba($colorCyan, .2);
  }
  &:-ms-input-placeholder {
    color: rgba($colorCyan, .2);
  }
  &:-moz-placeholder {
    color: rgba($colorCyan, .2);
  }
}

.title-main {
  color: $colorCyan;
  text-align: center;
  @include rem(font-size, 50px);
  font-weight: 100;
  text-transform: uppercase;
  @include rem(margin, 0 0 35px 0);
}

.title-sub {
  color: #fff;
  @include rem(font-size, 31px);
  p {
    margin: 0 0 5px 0;
  }
}

.text-grey {
  font-size: 18px;
  color: $colorGreyish;
  font-family: $fontOpenSans;
  font-weight: 400;
}

.section100 {
  @include rem(padding, 100px 0);
  @include break(mobile) {
    @include rem(padding, 60px 0);
  }
}

.list-circles {
  list-style: none;
  margin: 0;
  padding: 0;
  li {
    font-size: 18px;
    color: #fff;
    margin-bottom: 10px;
    &:before {
      font-family: $fontGnosis;
      content: '\63';
      font-size: 12px;
      position: relative;
      top: -1px;
      color: $colorGreen;
      margin-right: 10px;
      line-height: 18px;
    }
  }
}