.bg-wrapper {
  background: url("../images/main-bg.jpg") bottom center no-repeat;
  background-size: cover;
}

.home-top {
  padding: 10rem 0 8rem 0;
  .wallet {
    text-align: center;
  }
  .title-main {
    @include rem(margin, 50px 0 30px 0);
    font-weight: 400;
    @include break(tablet) {
      font-size: 9vw;
    }
  }

  .protected {
    text-align: center;
    @include rem(margin-top, 50px);
    .money {
      color: $colorGreen;
      @include rem(font-size, 100px);
      @include rem(margin, 50px 0);
      @include break(tablet) {
        font-size: 12vw;
      }
    }
  }

  .home-top-inner {
    .buttons {
      @include rem(margin-bottom, 50px);
      a {
        margin: 0 10px;
        width: 312px;

        @include break(tablet) {
          &:first-of-type {
            margin-bottom: 15px;
          }
        }

        &:first-of-type {
          margin-left: 0;
        }
        &:last-of-type {
          margin-right: 0;
        }
      }
      .icon-apple {
        font-size: 32px;
        top: 6px;
      }
      .icon-wallet {
        font-size: 32px;
        top: 8px;
      }
    }
    width: 650px;
    max-width: 100%;
  }
}

.trusted {
  color: #fff;
  text-align: center;
  background: #182A36;
  @include rem(padding, 0px 0 50px 0);
  .trusted-title {
    color: #fff;
    font-weight: 300;
    font-family: $fontMontserrat;
    margin: 0 0 40px 0;
    text-transform: uppercase;
    font-size: 22px;
  }

  .logo {
    text-align: center;
    height: 100px;

    @include break(desktop){
      flex-basis: 25%;
    }

    @include break(tablet) {
         flex-basis: 33.33%;
       }
    @include break(mobile) {
      flex-basis: 100%;
    }
    @include break(mobile-sm) {
      flex-basis: 100%;
    }

    img {
      position: relative;
      top: 50%;
      transform: translateY(-50%);

    }
  }
}

.main-videos {
  background: $colorBlue;
  .video {
    .video-inner {
      @include aspect-ratio(16, 9);
      border: 2px solid rgba($colorCyan, .4);
      position: relative;
      transition: $transition;
      &:hover {
        border-color: $colorCyan;
      }
      .overlay {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(#000, .6);
        text-align: center;
        cursor: pointer;
        color: #fff;
        transition: $transition;

        span {
          font-size: 65px;
          line-height: 1;
          display: inline-block;
          height: 65px;
          @include vertical-align();
        }
      }
      .thumbnail {
        width: 100%;
        height: 100%;
        background-size: cover;
        background-position: center;
      }
    }

    @include break(tablet) {
      flex-basis: 100%;
      margin-bottom: 30px;
      &:last-of-type {
        margin-bottom: 0;
      }
    }
    iframe {
      display: none;
      transition: $transition;
      &:hover {
        border-color: $colorCyan;
      }
    }
    .video-title {
      font-size: 20px;
      color: #fff;
      text-align: center;
      margin-top: 25px;
      font-weight: 400;
    }
  }
}

.main-faq {
  background: $colorBlueDark;
  .faq-block {
    margin-bottom: 30px;
    .faq-block-question {
      font-weight: 300;
      color: #087e96;
      font-size: 30px;
      margin-bottom: 8px;
    }
    .text-grey {
      font-size: 16px;
      font-weight: 600;
      line-height: 1.5;
    }
  }
}

.main-bugtracker {
  background: $colorBlue;
  text-align: center;
  .bug {
    font-size: 68px;
    color: $colorCyan;
    margin-bottom: 35px;
  }
  .inner {
    width: 630px;
    max-width: 100%;
  }
  .buttons {
    margin-top: 40px;
    a {
      margin: 0 10px;
      @include break(mobile) {
        &:first-of-type {
          margin-bottom: 10px;
        }
      }
    }
  }
}

.team {
  background: #ebeced;
  .team-title {
    color: $colorGrey;
    font-size: 80px;
    font-weight: 100;
    text-transform: uppercase;
    text-align: center;
    @include rem(margin, 0 0 60px 0);
  }

  .team-member {
    text-align: center;
    margin-bottom: 30px;
    flex-basis: 33.333%;
    @include break(mobile) {
      flex-basis: 100%;
    }
    .image {
      margin-bottom: 10px;
      img {
        width: 152px;
        height: auto;
      }
    }
    .name {
      color: $colorCyan;
      font-size: 20px;
      text-transform: uppercase;
      font-weight: 400;
    }
    .twitter-acc {
      margin-bottom: 3px;
      a {
        color: $colorCyan;
        font-weight: 400;
        font-size: 14px;
      }
    }
    .position {
      font-size: 14px;
      color: #111e26;
      margin-bottom: 10px;
      font-weight: 400;
    }
    .desc {
      color: #87949c;
      font-size: 14px;
      font-weight: 600;
      font-family: $fontOpenSans;
    }
  }
}

.newsletter {
  text-align: center;
  background: #14242e;
  padding: 4.4rem 0;
  .newsletter-title{
    font-weight: 400;
  }
  .form-input {
    width: 335px;
    margin: 25px 0;
    @include break(mobile) {
      width: 100%;
    }
  }
}

