*, *:before, *:after {
  @include box-sizing();
}

.container {
  margin-right: auto;
  margin-left: auto;
  width: 1000px;
  max-width: 100%;
}

.container-fluid {
  width: 100%;
}

.container, .container-fluid {
  padding: 0 25px;
}

.container, .clearfix, .row {
  &:before, &:after {
    content: " ";
    display: table;
    clear: both;
  }
}

.flex-row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -25px;
  margin-right: -25px;
}

.flex-column{
  flex: 1;
  padding-left: 25px;
  padding-right: 25px;
}
