.footer {
  background: #13222b;
  text-align: center;
   padding: 4.4rem 0 2.8rem;
  .footer-socials {
    font-size: 35px;
    a {
      display: inline-block;
      margin: 0 10px;
      transition: $transition;
      @include break(mobile) {
        margin: 0 7px;
      }
    }
    .facebook {
      @include darkenOnHover(#3e8ae4, 10%);
    }
    .twitter {
      @include darkenOnHover(#4bc2fd, 10%);
    }
    .youtube {
      @include darkenOnHover(#e54840, 10%);
    }
    .github {
      @include darkenOnHover(#2d3d4d, 10%);
    }
    .reddit {
      @include darkenOnHover(#ff4500, 10%);
    }
    .slack {
      @include darkenOnHover(#E01564, 10%);
    }
    .medium {
      @include darkenOnHover(#00ab6b, 10%);
    }
  }

  .footer-logo {
    @include rem(margin-top, 20px);
    .logo-bottom {
      margin-top: 2.5rem;
      text-transform: uppercase;
      font-size: 14px;
      display: block;
      font-family: $fontOpenSans;
      font-weight: 400;
    }
  }
}